import Vue from "vue";

const TemplateHoc = (component, options) => {
  return Vue.component(options.name, {
    render(createElement) {
      return createElement(component, {
        props: {
          ...this.$attrs,
          ...(options?.props?.(this) || {})
        },
        on: { ...this.$listeners },
        scopedSlots: this.$scopedSlots
      });
    },
    methods: options?.methods || {},
    ref: options.ref || null,
    inheritAttrs: false
  });
};
export default TemplateHoc;
